.app {
  width: 100%;
  height: 100%;
}

.ol-map__controls {
  position: absolute;
  padding: 10px 16px;
  top: 10px;
  left: 10px;
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  background: rgba(0, 0, 0, 0.5);
  color: #eee;
  border-radius: 8px;
  z-index: 1;
}
